.cart_button {
    background-color: rgb(137, 86, 66);
    border: none;
    border-radius: 40px;
    padding: 10px 20px;
    cursor: pointer;
    color: #fff;
    font-weight: 700;
    text-transform: uppercase;
    display: flex;
    align-items: center;
}

.cart_button i {
    margin: 0 10px;
}

.cart_button span {
    background-color: rgb(174, 141, 125);
    padding: 5px 20px;
    font-size: 18px;
    border-radius: 20px;
}