.cart_modal {
    top: 20vh;
    z-index: 2;
    position: fixed;
    width: 70%;
    background-color: #fff;
    left: 15%;
    border-radius: 10px;
}

.overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgb(0 0 0 / 62%);
    z-index: 1;
}

.cart_modal ul {
    padding: 10px 20px;
}

.cart_modal h2 {
    text-align: center;
}

.button_pay {
    padding: 5px 10px;
    text-transform: uppercase;
    font-weight: 700;
    cursor: pointer;
    color: #fff;
    background-color: rgb(255 74 74);
    border: none;
}

.button_pay:hover {
    background-color: rgb(255, 0, 0);
}

.total_price {
    font-weight: 700;
}