.section {
    padding: 20vh 20px;
    background: url(../assets/high-bg.jpeg);
    color: #fff;
    text-align: center;
}

.darky {
    padding: 19px 0;
    background-color: #08080b8f;
    border-radius: 21px;
    width: 70%;
    margin: auto;
}

.section .button {
    text-decoration: none;
    background-color: rgb(255 74 74);
    padding: 10px 20px;
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 700;
    border: none;
    color: #fff;
    cursor: pointer;
}

.section .button:hover {
    background-color: rgb(255, 0, 0);
}

.section p {
    margin-bottom: 30px;
}