.goods_section {
    padding: 20px 10%;
    background-color: rgb(57 45 41);
}

.goods_section h2 {
    text-align: center;
    color: #fff;
}

.goods_section ul {
    display: grid;
    padding: 0;
}

.goods_section li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    margin-top: 30px;
    list-style: none;
    background-color: #fff;
    border-radius: 30px;
}

.goods_section li button {
    background-color: rgb(255 74 74);
    padding: 5px 10px;
    text-transform: uppercase;
    font-weight: 700;
    border: none;
    color: #fff;
    cursor: pointer;
    display: block;
    margin: 10px auto 0 auto;
}

.goods_section li button:hover {
    background-color: rgb(255, 0, 0);
}

.goods_section li label {
    margin-right: 10px;
}

.goods_section span {
    color: red;
    font-weight: 700;
}