.cart_item {
    border-bottom: 1px solid #ff3434;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.cart_item button {
    background-color: rgb(255 74 74);
    padding: 5px 10px;
    text-transform: uppercase;
    font-weight: 700;
    border: none;
    color: #fff;
    cursor: pointer;
    margin: 10px auto 0 auto;
}

.amount {
    font-weight: 700;
    padding: 0px 10px;
}

.cart_item button:hover {
    background-color: rgb(255, 0, 0);
}

.cart_item .item_price {
    color: #ff3434;
    font-weight: 700;
}